.fashion__swiper_section {
   padding-left: 15px;
}

.fashion__swiper__title {
   position: $relative;
   gap: $size-20;

   .fashion__arrow_wrap {
      gap: 57px;
   }

   & h2 {
      font-size: $size-24;
   }

   & ._fashion__arrows {
      --width: 13px;
      --height: 23px;
      width: var(--width);
      height: var(--height);
      min-width: var(--width);
      min-height: var(--height);
      max-width: var(--width);
      max-height: var(--height);
      inset: unset;
      margin-top: unset;
      position: static;
      opacity: 0;
      transition: all 0.2s $ease;

      &.__prev {
      }

      &.__next {
         transform: rotate(180deg);
      }

      &:hover {
         opacity: 1;
      }

      &:active {
         & svg {
            & path {
               stroke: $color-000;
            }
         }
      }

      &::after {
         display: none;
      }
   }
}

._fashion-slider {
   & .swiper-slide {
      width: $auto;
      flex-shrink: 0;
   }
}

.fashion__slider_wrap {
   position: $relative;
   --width: 612px;
   --height: 660px;
   width: var(--width);
   max-width: var(--width);
   min-width: var(--width);
   height: var(--height);
   max-height: var(--height);
   min-height: var(--height);

   & img {
      object-fit: $cover;
   }
}

.fashion__hidden_block {
   --proc: 100%;
   position: $absolute;
   width: var(--proc);
   height: var(--proc);
   top: 0;
   left: 0;
}

.fashion__hidden_item {
   position: $absolute;

   &._headThings {
      top: 77px;
      left: 238px;

      & .fashion__hidden_info {
         top: 45px;
      }
   }

   &._bodyThings {
      top: 244px;
      left: 343px;

      & .fashion__hidden_info {
         top: 45px;
      }
   }

   &._handsThings {
      top: 389px;
      left: 174px;

      & .fashion__hidden_info {
         top: -110px;
      }
   }

   &._legsThings {
      top: 499px;
      left: 310px;

      & .fashion__hidden_info {
         top: -110px;
      }
   }

   & .fashion__hidden_info {
      position: $absolute;
      opacity: 0;
      pointer-events: $none;
      transition: all 0.3s $ease;

      &._show {
         opacity: 1;
         pointer-events: all;
      }
   }
}

.fashion__hidden_wrap {
   gap: $size-8;
   min-width: 237px;
   padding: $size-12 $size-16;
   background-color: $color-fff;

   & p {
      & .prev_price {
         padding-left: 4px;
         text-decoration: line-through;
         color: $color-8e8e8e;
         font-family: var(--second-family);
      }
   }
}

.fashion__color_list {
   gap: $size-4;

   & .card__color_label {
      --border-size: 14px;
      padding: 2px;

      &::after {
         --circle: 10px;
      }
   }
}

.fashion__btn {
   --size: 32px;
   width: var(--size);
   height: var(--size);
   min-width: var(--size);
   min-height: var(--size);
   max-width: var(--size);
   max-height: var(--size);
   opacity: 0.7;
   pointer-events: all;
   transition: all 0.2s $ease;

   & img {
      object-fit: fill;
   }

   &:hover,
   &:active,
   &._active {
      opacity: 1;
      transform: scale(1.2);
   }
}

.fashion__current_slide {
   position: $absolute;
   bottom: $size-30;
   left: $size-30;
}

.fashion__current_slide_link {
   --width: 34px;
   --height: 34px;
   background-color: $color-fff;
   padding: 7px 9px;
   width: var(--width);
   height: var(--height);
   min-width: var(--width);
   min-height: var(--height);
   max-width: var(--width);
   max-height: var(--height);
   gap: $size-6;
   border-radius: 100px;
   transition: drop-shadow 0.2s $ease;

   & span {
      display: $none;
      color: $color-000;
      font-size: $size-14;
      font-family: var(--second-family);
   }

   &:hover {
      --width: 188px;

      & span {
         display: flex;
         word-break: keep-all;
         white-space: nowrap;
      }
   }

   & svg {
      --size: 16px;
      width: var(--size);
      height: var(--size);
      min-width: var(--size);
      min-height: var(--size);
      max-width: var(--size);
      max-height: var(--size);
   }
}

@media screen and (max-width: 1024px) {
   .fashion__swiper__title {
      h2 {
         font-size: 20px;
      }
   }
}

@media screen and (max-width: 900px) {
   .fashion__slider_wrap {
      --width: 530px;
      --height: 530px;
   }

   .fashion__current_slide {
      bottom: 19px;
      left: 15px;
   }

   .fashion__hidden_item {
      &._headThings {
         top: 40px;
         left: 200px;
      }

      &._bodyThings {
         top: 185px;
         left: 288px;
      }

      &._handsThings {
         top: 300px;
         left: 160px;

         & .fashion__hidden_info {
            top: -110px;
         }
      }

      &._legsThings {
         top: unset;
         left: 270px;
         bottom: 50px;
      }
   }
}

@media screen and (max-width: 768px) {
   .fashion__swiper__title {
      & h2 {
         font-size: 17px;
         padding-bottom: 24px;
         padding-top: 10px;
      }
   }
}

@media screen and (max-width: 600px) {
   .fashion__slider_wrap {
      --width: 353px;
   }

   .fashion__swiper__title {
      & .fashion__arrow_wrap {
         display: $none;
      }
   }

   .fashion__hidden_item {
      &._headThings {
         top: 40px;
         left: 200px;

         & .fashion__hidden_info {
            left: unset;
            right: -40px;
         }
      }

      &._bodyThings {
         left: 210px;

         & .fashion__hidden_info {
            left: unset;
            right: -40px;
         }
      }

      &._handsThings {
         left: 70px;

         & .fashion__hidden_info {
            left: -35px;
         }
      }

      &._legsThings {
         left: 220px;
         bottom: 75px;

         & .fashion__hidden_info {
            left: -115px;
         }
      }
   }
}
.fashion__hidden_item {
   &.left {
      .fashion__hidden_info {
         left: 0;
         right: unset;
      }
   }
   &.right {
      .fashion__hidden_info {
         left: unset;
         right: 0;
      }
   }
}
